argdown-map code.language-argdown {
  color: #000;
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  padding: 1.25rem 1.5rem;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  line-height: 1.4;
  display: block;
}

.argvu code.language-argdown {
  font-variant-ligatures: discretionary-ligatures;
  font-feature-settings: "liga", "dlig";
  font-family: ArgVu Sans Mono Regular, Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  font-size: .8rem;
}

code.language-argdown .hljs-section {
  color: #6d6d5d;
  font-weight: bold;
}

code.language-argdown .hljs-meta {
  color: #9b9b85;
  font-weight: normal;
}

code.language-argdown .hljs-comment, code.language-argdown .hljs-quote {
  color: #998;
  font-style: italic;
}

code.language-argdown .hljs-string, code.language-argdown .hljs-tag {
  color: #3b95bc;
  font-weight: normal;
}

code.language-argdown .hljs-link {
  color: #134c66;
  font-weight: normal;
}

code.language-argdown .hljs-support {
  color: #4ea429;
  font-weight: bold;
}

code.language-argdown .hljs-attack {
  color: #c24545;
  font-weight: bold;
}

code.language-argdown .hljs-undercut {
  color: #c227a7;
  font-weight: bold;
}

code.language-argdown .hljs-contradiction {
  color: #c24545;
  font-weight: bold;
}

code.language-argdown .hljs-statement-title {
  color: #3b95bc;
  font-weight: bold;
}

code.language-argdown .hljs-argument-title, code.language-argdown .hljs-inference, code.language-argdown .hljs-argument-statement-nr {
  color: #134c66;
  font-weight: bold;
}

html {
  --argdown-bg-color: #fff;
  --argdown-border-color: #eee;
  --argdown-logo-color: #ccc;
  --argdown-button-bg-color: #3e8eaf;
  --argdown-button-bg-hover-color: #387e9c;
  --argdown-button-border-bottom-color: #38809d;
  --argdown-button-font-color: #fff;
}

argdown-map:not(:defined) [slot="source"] {
  display: none;
}

argdown-map:not(:defined) [slot="map"] {
  justify-content: center;
  margin-top: 40px;
  display: flex;
}

argdown-map:not(:defined)[initial-view="source"] [slot="map"] {
  display: none;
}

argdown-map:not(:defined)[initial-view="source"] [slot="source"] {
  display: block;
}

argdown-map:not(:defined) {
  background-color: #fff;
  border: 1px solid #eee;
  width: 100%;
  display: block;
}

argdown-map.zooming [slot="map"] svg {
  width: 100%;
  height: 100%;
}

argdown-map pre {
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  display: block;
  background-color: #fff !important;
}

.argdown-figure {
  border: 1px solid #dedede;
  border-radius: 6px;
  margin: 2rem 0;
  overflow: hidden;
}

.argdown-figure figcaption {
  border-top: 1px solid #dedede;
  padding: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.argdown-figure argdown-map {
  border: 0;
  height: 100%;
}
